.ontacts {
  flex: 1;
}

.ontacts__container {
  display: grid;
  grid-template-columns: max-content min-content 1fr;
  column-gap: 60px;
  padding: 0 60px 66px 124px;

  @include vp-1199 {
    grid-template-columns: max-content min-content max-content;
    justify-content: space-between;
    column-gap: 48px;
    padding: 0 180px 66px 133px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    row-gap: 29px;
    padding: 0 15px 66px 15px;
  }
}

.ontacts h3 {
  font-family: $ff-alumni;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin: 0;
  margin-bottom: 45px;
  padding: 63px 120px 0 120px;

  @include vp-1199 {
    font-size: 42px;
    letter-spacing: 1.26px;
    padding: 60px 133px 0;
    margin-bottom: 35px;
  }


  @include vp-767 {
    font-size: 32px;
    font-weight: 600;
    line-height: 32px;
    letter-spacing: 0.96px;
    margin-bottom: 44px;
    padding: 66px 15px 0 15px;
  }
}

.ontacts__address span,
.ontacts__work-schedule span,
.ontacts__phone-number span,
.ontacts__email span {
  display: block;
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
  margin-bottom: 13px;

  @include vp-1199 {
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 15px;
  }
}

.ontacts__address {
  margin-bottom: 23px;

  @include vp-1199 {
    margin-bottom: 30px;
  }

  @include vp-767 {
    margin-bottom: 0;
  }
}

.ontacts__address p,
.ontacts__work-schedule p {
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  margin: 0;

  @include vp-1199 {
    font-size: 18px;
    line-height: 18px;
  }
}

.ontacts__phone-number a,
.ontacts__email a {
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  margin: 0;
  display: block;
  color: $color-dark-blue;
  width: fit-content;

  &:hover {
    opacity: 0.8;
  }

  &:focus-visible {
    outline: 1px solid $color-dark-blue;
  }

  &:active {
    opacity: 1;
  }

  &:disabled {
    opacity: 0.3;
  }

  @include vp-1199 {
    font-size: 18px;
    line-height: 18px;
  }
}

.ontacts__decorative-line {
  width: 1px;
  height: 100%;
  background-color: #74819c;
  grid-column: 2;
  grid-row: 1 / 3;

  @include vp-767 {
    display: none;
  }
}

.ontacts__work-schedule {
  @include vp-767 {
    order: 0;
  }
}

.ontacts__phone-number {
  @include vp-767 {
    order: 1;
  }
}

.ontacts__email {
  @include vp-767 {
    order: 2;
  }
}
