.gym__container {
  display: grid;
  grid-template-columns: 36% 1fr;
  column-gap: 85px;
  position: relative;
  padding: 120px 103px 235px 103px;
  max-width: $max-width;
  margin: 0 auto;

  @include vp-1199 {
    grid-template-columns: none;
    grid-template-rows: 1fr auto;
    padding: 100px 45px 183px 45px;
  }

  @include vp-767 {
    text-align: center;
    padding: 67px 0 37px 0;
  }
}

.gym__video-container {
  display: block;
  position: absolute;
  top: 32.6em;
  left: 42.6em;
  transform: translate(-39%, -50%);

  @include vp-1199 {
    grid-row: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -2%);
  }

  @include vp-767 {
    grid-row: 3;
    position: relative;
    top: 0;
    left: 0;
    transform: none;
    width: 100%;
    height: calc(100% - 33px);
  }
}

.gym__video {
  width: 364px;
  height: 228px;
  object-fit: cover;

  @include vp-1199 {
    width: 266px;
    height: 167px;
  }

  @include vp-767 {
    min-width: 100%;
    height: 100%;
  }
}

.gym__image {
  margin: 0 auto;
  object-fit: cover;

  @include vp-767 {
    margin-bottom: 40px;
  }
}

.gym__info {
  grid-column: 1;
  padding-left: 60px;
  padding-top: 45px;

  @include vp-1199 {
    grid-row: 1;
    padding: 0;
  }

  @include vp-767 {
    padding: 0 0 0 15px;
  }
}

.gym h2 {
  font-family: $ff-alumni;
  font-size: 48px;
  font-weight: 600;
  letter-spacing: 1.44px;
  margin: 0;
  margin-bottom: 12px;
  line-height: 48px;

  @include vp-1199 {
    text-align: center;
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    padding: 5px 16px 0 0;
    margin-bottom: 3px;
  }

  @include vp-767 {
    text-align: left;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    padding: 0;
    margin-bottom: 8px;
  }
}

.gym span {
  display: block;
  font-size: 18px;
  font-weight: 400;
  line-height: 150%;
  margin-bottom: 39px;

  @include vp-1199 {
    text-align: center;
    margin-bottom: 34px;
  }

  @include vp-767 {
    text-align: left;
    margin-bottom: 25px;
  }
}

.gym p {
  font-size: 16px;
  font-weight: 300;
  line-height: 150%;
  margin: 0;
  margin-bottom: 9px;

  @include vp-1199 {
    text-align: center;
    padding: 0 50px 34px 50px;
  }

  @include vp-767 {
    text-align: left;
    padding: 0;
    padding-right: 42px;
    margin-bottom: 32px;
  }
}

.gym p:last-child {
  margin: 0;

  @include vp-1199 {
    display: none;
  }
}

.gym__button {
  background-image: url("../img/svg/play-video-desktop.svg");
  background-color: transparent;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  cursor: pointer;
  outline: none;

  &:hover,
  &:focus-visible {
    background-image: url("../img/svg/play-video-desktop-hover-focus.svg");
  }

  &:active {
    background-image: url("../img/svg/play-video-desktop-active.svg");
  }

  &:disabled {
    background-image: url("../img/svg/play-video-desktop-disabled.svg");
  }

  @include vp-1199 {
    background-image: url("../img/svg/play-video-tablet.svg");
    width: 73px;
    height: 73px;

    &:hover,
    &:focus-visible {
      background-image: url("../img/svg/play-video-tablet-hover-focus.svg");
    }

    &:active {
      background-image: url("../img/svg/play-video-tablet-active.svg");
    }
  }
}
