.accordion {
  margin: 0 auto 0 auto;

  & & {
    width: 90%;
    margin: 0 auto;
  }

  &__element:last-child {
    margin-bottom: 120px;

    @include vp-1199 {
      margin-bottom: 104px;
    }

    @include vp-767 {
      margin-bottom: 70px;
    }
  }

  &__element {
    margin: 0;
    padding: 26px 0 23px 0;
    border-bottom: 1px solid rgba(28, 51, 116, 0.2);

    @include vp-767 {
      padding: 21px 0 18px 0;
    }
  }

  &__element.is-active &__button {
    background-color: $color-dark-blue;
  }

  &__element.is-active &__button::after {
    content: "";
    width: 18px;
    height: 1px;
    background-color: $color-default-white;
  }

  &__button {
    background-color: transparent;
    border: 1px solid #1c3374;
    border-radius: 50%;
    width: 42px;
    height: 42px;
    margin-left: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    &:hover,
    &:focus-visible {
      background-color: $color-dark-blue;
      outline: none;
    }

    &:disabled {
      opacity: 0.3;
    }

    @include vp-767 {
      width: 32px;
      height: 32px;
      min-width: 32px;
    }
  }

  &__wrapper {
    padding: 0;
  }
}

.accordion__element.is-active .accordion__close-button-icon {
  display: none;
}

.accordion__element:first-child {
  border-top: 1px solid rgba(28, 51, 116, 0.2);
}

.accordion__close-button-icon {
  stroke: $color-dark-blue;
}

.accordion__button:hover .accordion__close-button-icon,
.accordion__button:focus-visible .accordion__close-button-icon {
  stroke: $color-default-white;
  outline: none;
}

.accordion__button-container {
  display: flex;
  align-items: center;
  cursor: pointer;
  border: none;
  background-color: transparent;
  width: 100%;
  padding: 0;

  &:hover .accordion__button,
  &:focus-visible .accordion__button {
    background-color: $color-dark-blue;
    outline: none;
  }

  &:hover .accordion__close-button-icon,
  &:focus-visible .accordion__close-button-icon {
    stroke: $color-default-white;
    outline: none;
  }
}

.accordion__button-container span:first-child {
  width: fit-content;
  text-align: left;
  color: $color-dark-blue;
  font-family: $ff-roboto;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  margin: 0;
  letter-spacing: 0.12px;

  @include vp-1199 {
    line-height: 26px;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 120%;
  }
}

.accordion__wrapper p {
  font-size: 18px;
  font-weight: 300;
  line-height: 140%;
  margin: 0;
  padding-top: 7px;

  @include vp-767 {
    font-size: 16px;
    line-height: 140%;
    padding-top: 12px;
  }
}

